.carousel {
  height: 40vh;
  .card-container {
    height: 38vh;
    position: relative;
    overflow: hidden;
    .card {
      height: 100%;
      width: 33.3%;
      transition: all 1s;
      position: absolute;
      opacity: 0.8;
      transform: scale(0.8);
      z-index: 998;
      cursor: pointer;
      user-select: none;
      img {
        height: 100%;
        width: 100%;
        border-radius: 5px;
      }
    }
  }

  .rects {
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    .rect {
      height: 2px;
      width: 20px;
      background-color: #ddd;
      margin-right: 8px;
      cursor: pointer;
    }
    .active {
    background-color: #04253C;
  }
  }

  
}
